___CSS_LOADER_EXPORT___.push([module.id, `.escher-container .button-panel {
  position: absolute;
  left: 4px;
  top: 20%;
  margin-top: -32px;
  padding-left: 0;
  touch-action: none;
}

.escher-container .button-panel>li {
  margin-top: 5px;
  display: block;

  /* these sometimes get overridden */
  margin-left: 0 !important;
}

.escher-container .grouping {
  display: block;
}

.escher-container .buttonGroup {
  display: block;
  margin-bottom: -1px;
  padding: 5px 0px;
  border-radius: 0;
}

.escher-container .grouping>.buttonGroup:first-child {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.escher-container .grouping>.buttonGroup:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.escher-container .button-panel>.grouping:last-child {
  margin-top: 4px;
}

.escher-container #currentMode,
.escher-container .active-button {
  background-image: linear-gradient(#8F4F3F,#834c3c 6%,#8d3a2d) !important;
}

.escher-container .buttonGroup.btn {
  margin-top: -1px;
}

.escher-container .button {
  border-radius: 4px;
}

.escher-container .button.btn, .escher-container .buttonGroup.btn {
  padding: unset;
  color: white!important;
  border: 1px solid #474949;
  background-image: linear-gradient(#4F5151, #474949 6%, #3F4141);
  background-color: white;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  font-size: 14px!important;
  font-weight: 400;
  width: 40px;
  height: 40px;
}

.escher-container .button-panel .button:active, .escher-container .buttonGroup label:active, .escher-container .button-panel .buttonGroup:active {
  background-image: linear-gradient(#3F4141, #474949 6%, #4F5151);
}

.escher-container .button-panel .fa {
  font-size: 24px;
}

/* Icons */
.escher-container .button-panel [class^='icon-'] {
  font-size: 23px;
}

.escher-container .kth-reaction-arrows {
  display: flex;
    width: 40px;
    flex-direction: row;
}

.escher-container .kth-reaction-arrows button.btn {
  width: 20px;
  height: 20px;
}

.escher-container .kth-reaction-arrows button.btn i {
  font-size: 12px;
}`, "",{"version":3,"sources":["webpack://./src/css/ButtonPanel.css"],"names":[],"mappings":"AAAA;EACE,kBAAkB;EAClB,SAAS;EACT,QAAQ;EACR,iBAAiB;EACjB,eAAe;EACf,kBAAkB;AACpB;;AAEA;EACE,eAAe;EACf,cAAc;;EAEd,mCAAmC;EACnC,yBAAyB;AAC3B;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,cAAc;EACd,mBAAmB;EACnB,gBAAgB;EAChB,gBAAgB;AAClB;;AAEA;EACE,2BAA2B;EAC3B,4BAA4B;AAC9B;;AAEA;EACE,8BAA8B;EAC9B,+BAA+B;AACjC;;AAEA;EACE,eAAe;AACjB;;AAEA;;EAEE,wEAAwE;AAC1E;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,cAAc;EACd,sBAAsB;EACtB,yBAAyB;EACzB,+DAA+D;EAC/D,uBAAuB;EACvB,kBAAkB;EAClB,sBAAsB;EACtB,eAAe;EACf,yBAAyB;EACzB,gBAAgB;EAChB,WAAW;EACX,YAAY;AACd;;AAEA;EACE,+DAA+D;AACjE;;AAEA;EACE,eAAe;AACjB;;AAEA,UAAU;AACV;EACE,eAAe;AACjB;;AAEA;EACE,aAAa;IACX,WAAW;IACX,mBAAmB;AACvB;;AAEA;EACE,WAAW;EACX,YAAY;AACd;;AAEA;EACE,eAAe;AACjB","sourcesContent":[".escher-container .button-panel {\r\n  position: absolute;\r\n  left: 4px;\r\n  top: 20%;\r\n  margin-top: -32px;\r\n  padding-left: 0;\r\n  touch-action: none;\r\n}\r\n\r\n.escher-container .button-panel>li {\r\n  margin-top: 5px;\r\n  display: block;\r\n\r\n  /* these sometimes get overridden */\r\n  margin-left: 0 !important;\r\n}\r\n\r\n.escher-container .grouping {\r\n  display: block;\r\n}\r\n\r\n.escher-container .buttonGroup {\r\n  display: block;\r\n  margin-bottom: -1px;\r\n  padding: 5px 0px;\r\n  border-radius: 0;\r\n}\r\n\r\n.escher-container .grouping>.buttonGroup:first-child {\r\n  border-top-left-radius: 4px;\r\n  border-top-right-radius: 4px;\r\n}\r\n\r\n.escher-container .grouping>.buttonGroup:last-child {\r\n  border-bottom-left-radius: 4px;\r\n  border-bottom-right-radius: 4px;\r\n}\r\n\r\n.escher-container .button-panel>.grouping:last-child {\r\n  margin-top: 4px;\r\n}\r\n\r\n.escher-container #currentMode,\r\n.escher-container .active-button {\r\n  background-image: linear-gradient(#8F4F3F,#834c3c 6%,#8d3a2d) !important;\r\n}\r\n\r\n.escher-container .buttonGroup.btn {\r\n  margin-top: -1px;\r\n}\r\n\r\n.escher-container .button 